<template>
  <div class="payMoney-view">
    <div v-html="payHtml"></div>
  </div>
</template>

<script>
export default{
  data () {
    return {
      params: {}, // 富有支付请求数据
      detail: {},
      payHtml: ''
    }
  },
  created () {
    this.getProduct() // 获得富有支付
    this.$http(this.path.payMoney(this.params)).then(res => {
      console.log(res.data.code)
      if (res.data.code === 0) {
        this.detail = res.data
        this.payHtml = this.detail.respMsg
        setTimeout(() => { document.forwardForm.submit() }, 1000)
        console.log(this.detail)
        localStorage.setItem('contractId', JSON.stringify(this.detail.contractId))
      }
    })
  },
  methods: {
    getProduct () {
      this.params = this.$route.params
      console.log(this.params)
    }
  }
}
</script>



