<template>
  <div class="failed-view bte8 container">
    <div class="tc">
      <img src="../../assets/pay/failed.png" alt="">
      <p class="text">支付失败</p>
    </div>
    <button class="btn" @click="returnPay()">重新支付</button>
  </div>
</template>

<script>

export default{
  data () {
    return {
      id: undefined // 产品id
    }
  },
  created () {
    this.id = JSON.parse(localStorage.getItem('productId'))
    console.log(this.id)
    this.$parent.head.title = '支付结果'
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
    this.$parent.head.back = () => { // 点击返回
      this.$router.push({
        name: `inputMoney`,
        query: {
          id: Number(this.id),
          isBack: true
        } // 产品id
      })
    }
  },
  methods: {
    returnPay () { // 回到输入金额
      this.$router.push({
        name: `inputMoney`,
        query: {
          id: Number(this.id),
          isBack: true
        } // 产品id
      })
    }
  }
}

</script>

<style lang="scss" scoped>
.failed-view {
  @include center($head-h, 0);
  font-family: PingFang-SC-Medium;
  background: $base-bg;
  border-top: 0.02rem solid #e4c471;
  .tc {
    margin-top: 1.2rem;
  }
  .text {
    line-height: 0.4rem;
    color: #ed4630;
  }
  .btn {
    position: absolute;
    bottom: 0.3rem;
    width: calc(100% - 0.4rem);
  }
}
</style>



