<template>
    <div class="body bt8">
      <div class="idCard">
        <div class="title">
          <img src="../../assets/my/idcard.png" >
          <p>上传身份证正反面</p>
        </div>
        <div class="drawing">
          <input type="file" accept="image/*" @change="upright($event)" id="up" ref="font">
          <input type="file" accept="image/*" @change="bottomright($event)" id="bottom" ref="remove">
          <label for="up">
            <img :src="upImg" >
          </label>
          <label for="bottom">
            <img :src="bottomImg" >
          </label>
        </div>
      </div>
      <button class="id-button" :class="{nd: isOn}" @click="updata">提交</button>
      <p class="message" v-if="isOn">请上传图片</p>
    </div>
</template>

<script>
import imgZ from '@/assets/my/img.png'
import imgF from '@/assets/my/imgfan.png'
export default {
  data () {
    return {
      upImg: imgZ,
      bottomImg: imgF
    }
  },
  methods: {
    upright: function () {
      let a = this.$refs.font.files[0] // 读取上传文件
      console.log(a)
      let img = new FormData() // 转换formdata对象
      img.append('realImage', a)
      console.log(img)
      let b = localStorage.getItem('id')
      this.$http(this.path.myRealCard(b, img, 'frontCard'))
        .then((rex) => {
          console.log(rex)
          if (rex.data.code === 0) {
            this.upImg = rex.data.data
          }
        })
    },
    bottomright: function () {
      let a = this.$refs.remove.files[0]
      console.log(a)
      let img = new FormData()
      img.append('realImage', a)
      console.log(img)
      let b = localStorage.getItem('id')
      this.$http(this.path.myRealCard(b, img, 'reverseCard'))
        .then((rex) => {
          console.log(rex)
          if (rex.data.code === 0) {
            this.bottomImg = rex.data.data
          }
        })
    },
    updata: function () {
      if (this.isOn) {
      } else {
        let a = this.$route.query
        let b = localStorage.getItem('id')
        let c = {
          realName: a.id,
          idCard: a.idCard,
          frontCard: this.upImg,
          reverseCard: this.bottomImg
        }
        console.log(c)
        this.$http(this.path.myReal(b, c))
          .then((rex) => {
            console.log(rex)
            if (rex.data.code === 0) {
              this.$router.push({
                name: 'main.recommend'
              })
            }
          })
      }
    }
  },
  computed: {
    isOn: function () {
      if (this.upImg !== imgZ && this.bottomImg !== imgF) {
        return false
      } else {
        return true
      }
    }
  },
  beforeCreate () {
    this.$parent.head.title = '实名认证'
    console.log(this.$route.query)
  }
}
</script>

<style lang="scss" scoped>
.body{
  @include center($head-h,0);
  background: $base-bg;
  font-family: PingFang-SC-Medium;
  padding-top: .1rem;
  .idCard{
    padding: 0 .15rem;
    background: #fff;
    .title{
      display: flex;
      align-items: center;
      height: .39rem;
      border-bottom: .01rem solid #D9D9D9;
      img{
        width: .19rem;
        margin-right: .05rem;
      }
      p{
        font-size: $tip-f;
        color: #666;
      }
    }
    .drawing{
      display: flex;
      justify-content: space-between;
      padding: .13rem 0;
      img{
        width: 1.65rem;
        height: 1.15rem;
      }
    }
  }
   .id-button{
    margin: .3rem .1rem 0 .1rem;
    width: calc(100vw - .1rem - .1rem);
    line-height: .44rem;
    background: $btn-bg;
    font-size: $enter-f;
    color: #fff;
  }
  .nd{
    background: #999;
  }
  #up{
    display: none;
  }
  #bottom{
    display: none;
  }
  .nd{
    background: #999;
  }
  .message{
    position: fixed;
    bottom: 1rem;
    left: 50%;
    margin-left: -$msg-w/2;
    width: $msg-w;
    line-height: $msg-h;
    text-align: center;
    background: $msg-bg;
    font-size: $msg-f;
    color: #fff;
  }
}
</style>



