<template>
  <div class="body bt8">
    <div class="head">
      <input type="text" v-model="text" placeholder="搜索" id="text" @input="cityText">
    </div>
    <div>
      <ul class="list">
        <li v-for="item of next" :key="item.index" @click="go(item)">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: '',
      ctiy: '',
      text: undefined
    }
  },
  methods: {
    cityText: function () {
      let a = /\w/g
      this.text = this.text.replace(a, '')
    },
    go: function (city) {
      let e = this.$route.query
      this.$router.push({
        name: 'myAddBank',
        query: {
          idCard: e.idCard,
          iphone: e.iphone,
          ctiy: city,
          bank: e.bank
        }
      })
    }
  },
  watch: {
    text (curVal, oldVal) {
      console.log(curVal, oldVal)
    }
  },
  computed: {
    next: function () {
      if (this.text) {
        let arr = []
        let _this = this
        this.list.map(function (item) {
          if (item.search(_this.text) !== -1) {
            arr.push(item)
          }
        })
        return arr
      } else {
        return this.list
      }
    }
  },
  filters: {
  },
  beforeCreate () {
    this.$parent.head.title = '选择城市'
    console.log(this.$route.query)
    // let e = this.$route.query
    // this.$parent.head.back = () => {
    //   this.$router.push({
    //     name: 'myAddBank',
    //     query: {
    //       idCard: e.idCard,
    //       iphone: e.iphone,
    //       ctiy: e.ctiy,
    //       bank: e.bank
    //     }
    //   })
    // }
  },
  created () {
    this.$http.get('/static/citys.json') // 加载城市
      .then((rex) => {
        console.log(rex)
        this.list = rex.data.citys
        console.log(this.list)
      })
    let e = this.$route.query
    this.$parent.head.back = () => {
      this.$router.push({
        name: 'myAddBank',
        query: {
          idCard: e.idCard,
          iphone: e.iphone,
          ctiy: e.ctiy,
          bank: e.bank
        }
      })
    }
  },
  beforeDestroy () {
    this.$parent.head.back = () => {
      this.$router.back(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    font-family: PingFang-SC-Medium;
    .head{
      display: flex;
      justify-content: center;
      align-items: center;
      height: .5rem;
      padding: 0 .15rem;
      background: #fff;
      input{
        flex: 1 0 ;
        line-height: .3rem;
        background: #eeeeee;
        border-radius: .02rem;
      }
    }
    .list{
      background: #fff;
      li{
        line-height: .39rem;
       margin-left: .17rem;
        font-size: .14rem;
        color: #666666;
        border-bottom: .01rem solid #E8E8E8
      }
    }
}
</style>



