<template>
    <div id="canvasBox" class="bte8">
        <div class="greet flex-bet bte8">
            <button class="greet-btn" @click="cancle">取消</button>
            <button class="greet-btn" @click="clear">清除</button>
            <button class="greet-btn" @click="upload">确定</button>
        </div>
        <p class="tip">
            <img src="../../assets/pay/tip.png" alt=""> 温馨提示：请在以下白色区域内写签名
        </p>
        <div class="canvas-box">
            <canvas></canvas>
        </div>
    </div>
</template>

<script>
import Draw from '../../../static/js/draw.js'
export default {
  data () {
    return {
      detail: undefined,
      params: {writeId: '签了', userSign: undefined}
    }
  },
  components: {
    Draw
  },
  mounted () {
    this.$parent.head.back = () => { // 点击返回
      sessionStorage.removeItem('myWrite')
      this.$router.back(-1)
    }
    this.canvasBox = document.getElementById('canvasBox')
    this.initCanvas()
  },
  methods: {
    initCanvas () {
      const canvas = document.querySelector('canvas')
      this.draw = new Draw(canvas)
    },
    clear () { // 清除
      this.draw.clear()
    },
    cancle () { // 取消返回合同
      sessionStorage.removeItem('myWrite')
      this.$router.back(-1)
    },
    upload () { // 确定生成签名
      const image = this.draw.getPNGImage()
      var file = this.dataURLtoBlob(image)
      console.log(file)
      var reader = new FileReader()
      reader.readAsDataURL(file)
      console.log(reader)
      var formdata = new FormData()
      formdata.append('file', file)
      this.$http(this.path.Upwrite(formdata)).then(res => { // 上传签名
        console.log(res.data.code)
        if (res.data.code === 0) {
          this.detail = res.data
          console.log(res.data.code, this.detail)
          this.params.userSign = this.detail.signUrl
          sessionStorage.setItem('myWrite', JSON.stringify(this.params))
          var myWrite = JSON.parse(sessionStorage.getItem('myWrite'))
          console.log(myWrite)
          this.$router.back(-1) // 返回合同
        }
      })
    },
    dataURLtoBlob (dataurl) {
      var arr = dataurl.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], {
        type: mime
      })
    }
  }
}
</script>

<style lang="scss" scoped>
#canvasBox {
  position: fixed;
  top: 0.5rem;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 0.2rem;
  width: 100% !important;
  height: 100% !important;
  background: #fff;
  z-index: 1;
}
.tip {
  line-height: 0.3rem;
  background: #fef0da;
  img {
    margin: 0 0.02rem 0.02rem 0.06rem;
  }
}
.greet {
  position: absolute;
  bottom: 0.5rem;
  left: 0;
  right: 0;
  padding: 0.1rem 0.2rem;
  user-select: none;
}
.greet-btn {
  width: 1rem;
  line-height: 0.45rem;
  text-align: center;
  background: #e0b677;
  font-size: 0.18rem;
  color: #fff;
  border-radius: 10px;
}
.greet select {
  font-size: 18px;
}
.canvas-box {
  position: absolute;
  top: 0.3rem;
  left: 0.2rem;
  right: 0.2rem;
  bottom: 1.19rem;
  cursor: crosshair;
  background: #fff;
  canvas {
    width: 100%;
    height: 100%;
  }
}
</style>



