<template>
  <div class="success-view bte8">
    <div class="tc">
      <img src="../../assets/pay/success.png" alt="">
      <p class="text">支付成功</p>
    </div>
    <div class="btn-box container">
      <button class="btn" @click="checDetail()">查看订单</button>
      <button class="btn btn-two" @click="returnMoney()">返回列表</button>
    </div>
  </div>
</template>

<script>

export default{
  data () {
    return {
      contractId: undefined
    }
  },
  created () {
    this.contractId = JSON.parse(localStorage.getItem('contractId'))
    console.log(this.contractId)
    this.$parent.head.title = '支付结果'
    this.$parent.head.isWhite = false
    this.$parent.head.isEnter = false
    this.$parent.head.isArrow = false
    this.$parent.head.back = () => {
      this.$router.push({
        name: 'main.money'
      })
    }
  },
  methods: {
    returnMoney () { // 回到输入金额
      this.$router.push({
        name: 'main.money'
      })
    },
    checDetail () { // 去投资详情
      this.$router.push({
        name: `successDetail`,
        query: {id: Number(this.contractId)} // 合同id
      })
    }
  }
}

</script>

<style lang="scss" scoped>
.success-view {
  @include center($head-h, 0);
  font-family: PingFang-SC-Medium;
  background: $base-bg;
  border-top: 0.02rem solid #e4c471;
  .tc {
    margin-top: 1.2rem;
  }
  .text {
    line-height: 0.4rem;
    color: #5bbc11;
  }
  .btn-box {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0.3rem;
    .btn {
      width: 100%;
    }
    .btn-two {
      margin-top: 0.05rem;
      background: $base-bg;
      border: 0.01rem solid #d7a85e;
      color: #e4c471;
    }
  }
}
</style>



