<template>
  <div class="body bte8">
    <div v-if="params">
      <div  class="bankcard" @click="auto(item.id)" v-for="item of params" :key="item.index">
        <div class="one">
          <div>
            <img :src="item.icon" >
            <div class="banktitle">
              <p>{{item.bankName}}</p>
              <p>储蓄卡</p>
            </div>
          </div>
          <p>{{item.bankCard|bankCard}}</p>
        </div>
        <div class="two">
          <div class="money">
            <p class="title-m">单笔限额</p>
            <p>{{item.singleLimited|money}}</p>
          </div>
          <div class="money">
            <p class="title-m">日限额</p>
            <p>{{item.dayLimited|money}}</p>
          </div>
        </div>
      </div>
      <div class="foot-main">
      <ke-fu :text='text'></ke-fu>
      <button class="btn" @click="addCard">添加银行卡</button>
      </div>
    </div>
    <div v-else class="bte8 no">
      <img src="../../assets/my/noCard.png" >
      <button class="btn" @click="addCard">添加银行卡</button>
    </div>
    <pop :pop='pop'></pop>
    <transition name="fade">
    <p v-if="isOn" class="message">{{message}}</p>
    </transition>
  </div>
</template>

<script>
import pop from '../pop' // 弹窗
import keFu from './myKe'
export default {
  data () {
    return {
      params: '',
      realName: '',
      pop: {},
      text: '银行卡',
      message: '',
      isOn: false,
      vi: true
    }
  },
  methods: {
    auto: function (id) { // 点击设置默认银行卡
      console.log(id)
      this.pop = { // 更改弹窗样式功能
        title: '',
        doText: '确定',
        callback: () => {
          let a = {
            cardId: id
          }
          let e = localStorage.getItem('id')
          this.$http(this.path.myAutoCard(e, a))
            .then((rex) => {
              console.log(rex)
              console.log(rex.data.code === 0)
              if (rex.data.code === 0) {
                this.rest()
                this.pop.isOn = false
                this.message = '设置成功'
                this.isOn = true
                let e = setInterval(() => {
                  this.isOn = false
                  clearTimeout(e)
                }, 2500)
              }
            })
        },
        main: ['默认使用该银行卡?'],
        isOn: true
      }
    },
    rest: function () { // 重置弹窗
      this.isOn = false
      this.message = ''
    },
    addCard: function () { // 添加银行卡按钮
      if (this.realName) {
        console.log('应届')
        console.log(this.params.length)
        if (this.params.length === 2) {
          if (this.vi) {
            this.rest()
            let e = ''
            this.message = '最多添加两张银行卡'
            this.vi = false
            this.isOn = true
            e = setInterval(() => {
              this.isOn = false
              this.message = ''
              this.vi = true
              clearTimeout(e)
            }, 2500)
          }
        } else {
          this.$router.push({name: 'myAddBank'})
        }
      } else { // 为实名的时候的操作
        this.pop = { // 更改弹窗样式功能
          title: '',
          doText: '确定',
          callback: () => {
            this.$router.push({name: 'myReal'})
          },
          main: ['尚未填写身份信息，如否前往填写'],
          isOn: true
        }
      }
    }
  },
  components: {
    pop, keFu
  },
  filters: {
    money: function (val) { // 金钱过滤器
      if (val) {
        return val.substr(0, val.length - 3) + '万'
      }
    }
  },
  beforeCreate () {
    // this.$parent.head.title = '银行卡管理'
    // if (this.$route.query.id === -2) {
    //   this.$parent.head.back = () => {
    //     this.$router.push({name: 'main.my'})
    //   }
    // }
  },
  created () {
    this.$parent.head.title = '银行卡管理'
    console.log(this.$route.query.id === '-2')
    if (this.$route.query.id === '-2') {
      this.$parent.head.back = () => {
        this.$router.push({name: 'main.my'})
      }
    }
    let e = localStorage.getItem('id')
    this.$http(this.path.myBank(e)) // 获得银行卡信息
      .then((rex) => {
        console.log(rex.data)
        this.params = rex.data.data
        console.log(this.params)
        if (rex.data.code === 0) { // 根据code码来判断是否实名
          this.realName = true
        }
      })
  },
  beforeDestroy () {
    this.$parent.head.back = () => {
      this.$router.back(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
  .body{
    @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    >div{
      padding: 0 .15rem;
    }
    .bankcard{
      height: 1.18rem;
      margin-top: .1rem;
      background: #4a7eb8;
      border-radius: .05rem;
      padding: .15rem .1rem 0 .1rem;
      color: #fff;
      .one{
        display: flex;
        justify-content: space-between;
        margin-bottom: .21rem;
        >div{
          display: flex;
          justify-content: flex-start;
          >img{
            width: .31rem;
            height: .31rem;
            margin-right: .06rem
          }
          .banktitle{
            p{
              &:nth-child(1){
                font-size: $title-f;
              }
              &:nth-child(2){
                font-size: $sf;
                color: rgba(255, 255, 255, 0.562);
              }
            }
          }
        }
      }
      .two{
        margin-left: .37rem;
        .money{
          display: flex;
          font-size: $sf;
          .title-m{
            width: .5rem;
            text-align-last: justify;
          }
          p{
            &:nth-child(2){
              margin-left: .1rem;
            }
          }
        }
      }
    }
    .foot-main{
      position: absolute;
      bottom: .25rem;
      left: .15rem;
      right: .15rem;
      text-align: center;
      button{
        display: block;
        width: 100%;
        margin-top: .1rem;
      }
    }
    .no{
      text-align: center;
      width: 1.71rem;
      margin: 2rem auto 0 auto;
      padding: 0;
      >img{
        width: 1.24rem;
      }
      button{
        width: 1.71rem;
        margin-top: .1rem;
      }
    }
  .message{
    position: absolute;
    bottom: 1.5rem;
    left: 50%;
    margin-left: -.55rem;
    width: 1.1rem;
    line-height: .25rem;
    font-size: $sf;
    color: #fff;
    background: rgba(3, 3, 3, .6);
    text-align: center
  }
  }
</style>



