<template>
    <div class="body">
      <ul class="one-list">
        <li>
          <p>姓名</p>
          <p>{{realName.realName}}</p>
        </li>
        <li>
          <p>身份证号</p>
          <p>{{realName.idCard|id18}}</p>
        </li>
        <li>
          <p>城市</p>
          <div @click="go('myCtiy')">
            <p>{{params.ctiy}}</p>
            <i class="fa fa-angle-right"></i>
          </div>
        </li>
        <li @click="isbankO">
          <p>开户行</p>
          <div>
          <p>{{params.bank}}</p>
          <i class="fa fa-angle-right"></i>
          </div>
        </li>
        <li>
          <p>银行卡号</p>
          <input type="text" @input="idCardZ" v-model="params.idCard" placeholder="选择持卡人银行账户">
        </li>
        <li>
          <p >预留手机</p>
          <input type="text" @input="iphoneZ" v-model="params.iphone" placeholder="输入银行预留手机号">
        </li>
      </ul>
      <button class="btn" :class="{isb: isbutton}" @click="add">确认添加</button>
      <p class="message">{{message}}</p>
      <div class="bank" @click.self="isbankF" v-show="isbank" >
        <div class="wrapper bank-list" ref="wrapper">
          <ul class="content">
            <li v-for="item of bankList" :key="item.index" @click="listB(item.bankName)">
              <img :src="item.icon" >
              <span>{{item.bankName}}</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default{
  data () {
    return {
      realName: {},
      params: {
        idCard: '',
        iphone: '',
        ctiy: '选择城市',
        bank: '选择开户行'
      },
      bankList: '',
      isbank: false,
      message: '请填写完整信息'
    }
  },
  methods: {
    idCardZ: function () {
      let a = /\D/g
      this.params.idCard = this.params.idCard.replace(a, '')
    },
    iphoneZ: function () {
      let a = /\D/g
      this.params.iphone = this.params.iphone.replace(a, '')
    },
    go: function (url) {
      this.$router.push({
        name: url,
        query: {
          idCard: this.params.idCard,
          iphone: this.params.iphone,
          ctiy: this.params.ctiy,
          bank: this.params.bank
        }
      })
    },
    isbankO: function () {
      this.isbank = true
    },
    isbankF: function () {
      this.isbank = false
    },
    listB: function (bank) {
      console.log(bank)
      this.params.bank = bank
      this.isbank = false
    },
    add: function () {
      if (!this.isbutton) {
        console.log(2111)
        let a = {
          city: this.params.city,
          bankName: this.params.bank,
          bankCard: this.params.idCard,
          bankPhone: this.params.iphone
        }
        let b = localStorage.getItem('id')
        this.$http(this.path.myAddBankCard(b, a))
          .then((rex) => {
            console.log(rex)
            if (rex.data.code === 0) {
              this.message = '添加成功'
              setInterval(() => {
                this.$router.push({name: 'myBank'})
              }, 2000)
            }
          })
      }
    }
  },
  watch: {
    isbank () {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.wrapper, {click: true})
      })
    }
  },
  computed: {
    isbutton: function () {
      let params = this.params
      if (params.idCard && params.iphone && params.ctiy !== '选择城市' && params.bank !== '选择开户行') {
        return false
      } else {
        return true
      }
    }
  },
  components: {
    BScroll
  },
  beforeCreate () {
    // this.$parent.head.title = '添加银行卡'
    // this.$parent.head.back = () => {
    //   this.$router.push({name: 'myBank', query: {id: -2}})
    // }
  },
  created () {
    this.$parent.head.title = '添加银行卡'
    this.$parent.head.back = () => {
      this.$router.push({name: 'myBank', query: {id: -2}})
    }
    let a = localStorage.getItem('id')
    this.$http(this.path.myAddBankG(a)) // 读取添加银行的默认信息
      .then((rex) => {
        console.log(rex.data)
        this.realName = rex.data.data
      })
    let b = this.$route.query
    console.log(b)
    console.log(b.idCard || b.iphone || b.ctiy || b.bank)
    if (b.idCard || b.iphone || b.ctiy || b.bank) {
      this.params = b
    }
    this.$http(this.path.myBankList)
      .then((rex) => {
        console.log(rex)
        this.bankList = rex.data.data
      })
  },
  beforeDestroy () {
    this.$parent.head.back = () => {
      this.$router.back(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    font-family: PingFang-SC-Medium;
    background: $base-bg;
    font-family: PingFang-SC-Medium;
    .one-list{
      margin-top: 0.1rem;
      padding: 0 .15rem;
       background: #fff;
      li{
        display: flex;;
        justify-content: space-between;
        align-items: center;
        height: .6rem;
        border-bottom: 0.01rem solid #E8E8E8;
        >p{
          &:nth-child(1){
          width: .7rem + .15rem;
          padding-right: .15rem;
          line-height: .14rem;
          border-right: .01rem solid #E8E8E8;
          font-size: $mf;
          color: #333333;
          text-align-last: justify;
          }
        }
        >div,>p:nth-child(2){
          flex: 1 0;
          padding-left: .15rem;
          line-height: .6rem;
          font-size: $tip-f;
          color: #999999;
        }
        >div{
          display: flex;
          justify-content: space-between;
          align-items: center;
          >i{
            color:#D4D4D4;
            font-size: .2rem;
          }
        }
        >input{
          flex: 1 0;
          padding-left: .15rem;
          font-size: $tip-f;
          line-height: .6rem;
          color: #333;
        }
      }
    }
  .bank{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(3, 3, 3,.5);
    z-index: 2;
    .bank-list{
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: .5rem*7 + .25rem;
      background: #fff;
      overflow: hidden;
      ul{
        font-size: $title-f;
        li{
          display: flex;
          align-items: center;
          height: .5rem;
          padding-left: .15rem;
          img{
            width: .2rem;
            height: .2rem;
          }

        }
      }
    }
  }
  button{
    position: absolute;
    bottom: .25rem;
    left: .15rem;
    width: calc(100vw - .15rem - .15rem);
  }
  .isb{
    background: #666;
  }
  .message{
    position: absolute;
    bottom: 1rem;
    left: 50%;
    width: 1.2rem;
    margin-left: -.6rem;
    background: rgba($color: #333, $alpha: .5);
    font-size: $sf;
    color: #fff;
    line-height: .3rem;
    text-align: center
  }
}
</style>



