<template>
    <div class='body'>
        <div v-if="list">
          <div class="message" v-for="item of list" :key="item.index" >
            <div class="head">
                <p class="title">投资成功</p>
                <p class="data">2016-11-07</p>
            </div>
            <div class="main">
                <p class="info">新手体验计划已购买成功</p>
                <i class="fa fa-angle-right tr"></i>
           </div>
          </div>
        </div>
        <div v-else class="no-message">
            <img src="../../assets/money/continue.png" >
            <button class="btn" @click="angin">暂无数据</button>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      list: ''
    }
  },
  methods: {
    angin: function () {
      location.reload()
      console.log(0)
    }
  },
  beforeCreate () {
    this.$parent.head = {
      isWhite: false, // 箭头和标题颜色
      isEnter: false, // 标题字体
      title: '消息中心', // 标题内容
      isArrow: false, // 箭头显隐
      back: () => { // 点击返回
        this.$router.back(-1)
      }
    }
    let e = localStorage.getItem('id')
    this.$http(this.path.myMessage(e))
      .then(rex => {
        console.log(rex)
        console.log(rex.data.code === 0)
        if (rex.data.code === 0) {
          if (rex.data.data.length >= 1) {
            this.list = rex.data.data
            console.log(this.list === undefined)
          }
        }
      })
  }
}
</script>

<style lang="scss" scoped>
.body{
    @include center($head-h,0);
    background: $base-bg;
    .message{
        margin: .1rem 0;
        background: #fff;
        .head{
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: .33rem;
            padding: 0 .15rem ;
            border-bottom: .01rem solid $base-bg;
            .title{
                color:  $news-color;
            }
            .data{
                font-size: $sf;
                color: $list-color;
            }
        }
        .main{
            display: flex;
            justify-content: space-between;
             align-items: center;
            height: .46rem;
            padding: 0 .15rem ;
            .info{
                font-size: $sf;
                font-family: Adobe Heiti Std R;
            }
            i{
                flex-basis: .4rem;
                line-height: .5rem;
                font-size: $sf;
                color: #D4D4D4;
            }
        }
    }
    .no-message{
        position: absolute;
        top: 1.27rem;
        left: 50%;
        display: flex;
        flex-direction: column;
        align-content: center;
        margin-left: -.81rem;
        width: 1.62rem;
        img{
            width: 1.25rem;
            height: 1.2rem;
            margin-bottom: .3rem;
        }
    }
}
</style>



